import { Link, Form } from "@remix-run/react";

export default function PostCard({ post }) {
  const dateTime = new Date(post.createdAt);
  const date = dateTime.getDate();
  const month = dateTime.getMonth() + 1;
  const hours = dateTime.getHours();
  const minutes = dateTime.getMinutes();
  console.log(post);
  function handleLike(e) {
    e.stopPropagation();
  }
  return (
    <>
      <div className="mx-auto text-center bg-slate-300 w-2/5 rounded-lg mb-8 drop-shadow-lg">
        <Link to={`/${post.user?.username}/${post._id}`}>
          <p>
            Posted by <span className="font-bold">{post.user?.username}</span>
          </p>
          <img
            src={post.user?.avatar}
            className="w-10 h-10 rounded-full mx-auto mt-5"
          />
          <h2 className="font-bold text-lg p-5 pl-10 text-left">
            {post.title}
          </h2>
          <p className="pb-5 text-left pl-10">{post.description}</p>
          <img
            src={post.image}
            alt={post.title}
            className="w-52 mx-auto  rounded-lg"
          />

          <p>Posted {`${date}/${month}  ${hours}:${minutes}`}</p>
        </Link>
        <Form method="post">
          <input type="hidden" name="postId" value={post._id} />
          <button
            onClick={handleLike}
            type="submit"
            name="_action"
            value="like"
          >
            Likes:
            {post.likes.length}
          </button>
        </Form>
      </div>
    </>
  );
}
